.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2em 4em;
}

.nav-mobile {
  display: none;
  padding: 2em 4em;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  color: var(--black);
}

.nav-dropdown {
  color: black;
  font-size: 3em;
}

.nav-dropdown-content {
  position: absolute;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: 1em;
  top: 3em;
  color: var(--black);
  width: 100%;
  padding: 2em;
  font-size: 2em;
  background-color: white;
  box-shadow: 0 2em 2em var(--shadow-semi-dark);
  z-index: 100;
}

@keyframes nav-dropdown {
  0% {
    transform: translateY(-10em);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.nav-dropdown-content li {
  list-style-type: none;
  opacity: 0.9;
  margin-left: 1em;
}

.nav-dropdown-content p {
  padding: 0.5em 0;
  font-weight: bold;
}

.dropdown-button {
  background-color: var(--blue);
  border: transparent;
  border-radius: 1em;
}

* > * a {
  color: var(--black);
  text-decoration: none;
}
.logo {
  margin-right: 5em;
}
.logo img {
  max-height: 4em;
}
.link {
  display: flex;
  align-items: center;
  max-height: 100px;
  list-style: none;
  font-weight: bold;
  color: var(--white-min);
  font-size: 1.25em;
}
.link .active {
  color: var(--white);
  border-bottom: 0.2em solid var(--white);
}
.link .link-txt:hover {
  color: var(--red);
  cursor: pointer;
  border-bottom: 0.2em solid var(--red);
}
.link .link-txt {
  color: var(--white);
  margin: 0 1em;
  padding: 1em 0;
  transition: 0.2s;
}
.login {
  display: flex;
  justify-content: center;
  height: 100%;
  align-content: center;
}
.login .button {
  border: none;
  border-radius: 0.75em;
  padding: 0.5em 1em;
  margin: 0;
  background-color: var(--white);
  font-weight: bold;
  font-size: 1.25em;
  color: var(--blue);
  transition: 0.2s;
}
.login .button:hover {
  background-color: var(--white-blue);
  cursor: pointer;
  box-shadow: 0.5em 0.5em 3em var(--shadow-light);
  color: var(--black-blue);
}
.dropdown {
  position: relative;
}
.popup {
  position: absolute;
  background-color: var(--white);
  border-radius: 0.5em;
  color: var(--black-blue);
  padding: 0.5em;
  box-shadow: 1em 1em 4em var(--shadow-dark);
  animation: popup 0.2s;
  z-index: 100;
}
.popup-link {
  padding: 1em 2em;
  border-radius: 0.5em;
  transition: 0.1s;
  color: var(--black-blue);
}
.popup-link:hover {
  background-color: var(--white-blue);
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .nav {
    display: none;
  }
  .nav-mobile {
    display: flex;
  }
}

@keyframes popup {
  0% {
    transform: translateY(-5em);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
  }
}
