.caleg {
  padding: 6rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 5em;
  height: 100%;
  background-color: var(--white);
}

.card {
  background-color: white;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  width: 15rem;
  transition: 0.2s;
  box-shadow: 1em 1em 2em var(--shadow-semi-dark);
}

.card:hover {
  cursor: pointer;
  filter: brightness(0.9);
}

.card > img {
  max-height: 15rem;
  object-fit: cover;
  border-radius: 2em;
  width: 100%;
}

.card > .nomer {
  background-color: var(--blue);
  color: var(--white);
  position: relative;
  display: flex;
  width: fit-content;
  margin: 1em 0;
  justify-content: center;
  transform: translateX(-1em);
  border-radius: 1rem;
  font-weight: bold;
  font-size: 1.25em;
  padding: 0.75rem;
  display: flex;
}

.card > .nama {
  display: flex;
  justify-content: center;
  font-size: 1.75em;
  color: var(--black);
  font-weight: bold;
  margin-bottom: 1em;
  text-transform: uppercase;
}

.card-detail {
  display: flex;
  flex-direction: row;
  width: 75%;
  padding: 2rem;
  background-color: white;
  border-radius: 2rem;
  box-shadow: 1em 1em 2em var(--shadow-semi-dark);
}

.card-detail > .detail-img {
  display: flex;
  justify-content: center;
}

.card-detail img {
  width: 50%;
  object-fit: contain;
  filter: drop-shadow(0 2em 2em var(--shadow-dark));
}

.card-detail > .detail {
  flex-basis: 100%;
}

.detail-table td,
tr {
  padding: 0.5em;
  font-size: 1.25em;
  color: var(--black);
  text-align: left;
}

.detail-table .subjek {
  font-weight: bold;
  width: 10%;
}

.detail > .close-preview {
  padding: 1em 2em;
  background-color: var(--blue);
  border: transparent;
  border-radius: 1em;
  transition: 0.2s;
}

.close-preview:hover {
  filter: brightness(1.3);
}

@media screen and (max-width: 768px) {
  .card-detail {
    flex-direction: column;
  }
  .card {
    width: 100%;
    max-height: 100vh;
  }
  .card > img {
    max-height: 40rem;
  }
  .card > .nomer {
    font-size: 2em;
  }
  .card-detail {
    gap: 4em;
  }
  .caleg {
    padding: 4em;
  }
}

@media screen and (max-width: 480px) {
  .caleg {
    padding: 2em;
  }
  .detail-table td {
    display: flex;
  }
}

@media screen and (max-width: 1024px) {
  .card-detail {
    width: 100%;
  }
}
